.container {
    position: absolute;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    background-color: black;

    user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -o-user-select: none;
    -webkit-user-drag: none;
}

.entityContainer {
    position: absolute;
}
