
.projectsContainer{
    
    display: flex;
    margin: 1%;
    height: 90%;
    justify-content: center;
}

.projectsItemList{
    
    display: flex;
    overflow: scroll;
    flex-direction: column;
    align-items: stretch;
}

.projectContainer{
    display: flex;
    flex-direction: column;

    flex-wrap: wrap;

    border-style: solid;
    border-color: #31CB00;
    border-radius: 1px;

    padding: 10px;
    margin: 5px;

    background-color: rgba(0, 0, 0, 0.37);
    backdrop-filter: blur(2px);
}