.tabContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    padding-bottom: 3%;

    pointer-events: auto;
}

.blur {
    backdrop-filter: blur(2px);
    background-color: rgba(0, 0, 0, 0.5);
}

.tabContentContainer{
    height: 90%;
    width: 100%;
    border-radius: 15px;
    transition: transform 0.3s ease-in-out;
}

.tabScreenContainer{
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: 1;
    overflow: hidden;
    top:0;
    border-radius: 15px;
    pointer-events:none;
}

.tabScreenStyle {
    pointer-events: none;
    position: relative;
    width: 100%;
    height: 200%;

    transition: top 5s ease;
}

.tabContentInner{
    flex:1;
    overflow: scroll;
    height: 98%;
    pointer-events: auto;
}

.tabHandler{
    display: flex;
    flex-direction: column;
    height: 95%;
    width: 100%;
    position: relative;
    top: 1%;
}
