.abutton {
    padding: 8px 8px;  
    /*font-size: 5vw;*/
    text-align: center;
    font-family: 'Courier New', Courier, monospace;
    font-weight: bold;

    cursor: pointer;
    outline: none;
    color: #fff;

    flex: 1;

    border: none;
    border-radius: 15px;

    margin: 5px 8px 5px;
}

/* If the screen size is 601px wide or more, set the font-size of <div> to 80px */
@media screen and (min-width: 901px) {
  .abutton {
    font-size: 2vw;
  }
}

/* If the screen size is 600px wide or less, set the font-size of <div> to 30px */
@media screen and (max-width: 900px) {
  .abutton {
    font-size: 5vw;
  }
}
  
.abutton:hover {
  background-color: rgb(172, 36, 36);
}
  
.abutton:active {
  background-color:rgb(172, 36, 36);
  box-shadow: 0 5px rgb(119, 26, 26);
  transform: translateY(4px);
}

.selected {
  background-color:rgb(172, 36, 36);
  box-shadow: 0 5px rgb(119, 26, 26);
  transform: translateY(4px);
}

.unselected {
  background-color: rgba(172, 36, 36, 0.247);
  box-shadow: 0 9px rgba(117, 34, 34, 0.678);
}