.flip-card-container {
    position: relative;
    background-color: transparent;
    width: 100%;
    height: 100%;

    padding: 0;
    border-width: 0;
    cursor: pointer;
    outline: 0;
}

.flip-card-flipper{
    position: relative;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
}

.flip-card-front, .flip-card-back {
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    transform-style: preserve-3d;
    
}

.flip-card-back{
    position: relative;
    transform: rotateY(180deg);
    top: -100%;
}