

.aboutMeContainer{
    height: 100%;
    width: 100%;

    display: flex;
    flex-direction: column;
    align-content: space-between;
}

.tntContainer{
    display: flex;
    flex-direction: column;
    align-items:center;
    padding:10px;
}

.tntItemsContainer{
    overflow: scroll;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: stretch;

    border-style: solid;
    border-top-width: 1px;
    border-bottom-width: 1px;
    border-left-width: 0px;
    border-right-width: 0px;
    border-color: #31CB00;
}

.tntLangContainer{
    width:100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding-top: 10px;
    padding-bottom: 10px;
    margin: 5px;

    border-style:solid;
    border-radius: 5px;
    border-color: #31CB00;
    border-width:3px;
}

.tntToolList{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    justify-content: space-evenly;
    align-items: center;
}

.tntToolName {
    width: 100%;
    margin: 1px;
    color:#31CB00;
    font-family: 'Lucida Console';
    font-weight: bold;
}